.hero {
    position: relative;
    height: 100vh;
    background-image: url('../../Components/Assets/Frontend_Assets/bg.jpg');
    background-size: cover;
    background-position: center;
    display: flex;
}

/* Black Overlay */
.hero::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Black overlay with opacity */
    z-index: 1;
}

.hero-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;
    position: relative;
    z-index: 2; /* Place content above the overlay */
}

.hero-left h2 {
    color: #00724c;
    font-size: 26px;
    font-weight: 600;
}

.hero-left h2 span {
    background-color: white;
}

.hero-left p {
    color: white;
    font-size: 80px;
    font-weight: 700;
}

.hero-hand-icon {
    display: flex;
    align-items: center;
    gap: 20px;
}

.hero-hand-icon img {
    width: 105px;
}

.hero-latest-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #01a971;
    color: white;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
}

.hero-latest-btn:hover {
    background: #00724c;
    color: white;
}

.hero-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 2;
}

/* Responsive Styles */
@media (max-width:1280px) {
    .hero-left {
        padding-left: 100px;
    }
    .hero-left h2 {
        font-size: 22px;
    }
    .hero-left p {
        font-size: 60px;
    }
    .hero-hand-icon img {
        width: 80px;
    }
    .hero-latest-btn {
        gap: 10px;
        width: 250px;
        height: 60px;
        margin-top: 20px;
        font-size: 18px;
    }
    .hero-right img {
        width: 500px;
    }
}

@media (max-width:1024px) {
    .hero-left {
        padding-left: 80px;
    }
    .hero-left h2 {
        font-size: 20px;
    }
    .hero-left p {
        font-size: 50px;
    }
    .hero-hand-icon img {
        width: 65px;
    }
    .hero-latest-btn {
        width: 220px;
        height: 50px;
        font-size: 16px;
    }
    .hero-right img {
        width: 400px;
    }
}

@media (max-width:800px) {
    .hero-left {
        padding-left: 30px;
    }
    .hero-left h2 {
        font-size: 16px;
    }
    .hero-left p {
        font-size: 40px;
    }
    .hero-hand-icon img {
        width: 50px;
    }
    .hero-latest-btn {
        width: 175px;
        height: 40px;
        font-size: 13px;
    }
    .hero-right img {
        width: 300px;
    }
}

@media (max-width:500px) {
    .hero {
        flex-direction: column;
    }
    .hero-right {
        display: none;
    }
    .hero-left h2 {
        font-size: 18px;
    }
    .hero-left p {
        font-size: 50px;
    }
    .hero-hand-icon img {
        width: 55px;
    }
    .hero-latest-btn {
        width: 200px;
        height: 45px;
        font-size: 14px;
    }
}
